@import url(http://fonts.googleapis.com/earlyaccess/notosanskr.css);
@font-face {
  font-family: "Oswald-bold";
  src: url(/static/media/Oswald-Bold.e991dea9.ttf) format("truetype");
}

body {
  margin: 0;
  font-family: "Noto Sans KR", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.carousel-slider img {
  transform: scale(1.1);
  margin: 30px;
  margin-left: auto;
  margin-right: auto;
  max-height: 20vh;
  opacity: 100%;
  position: relative;
  border: 5px solid;
}

.div-slider {
  padding-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  outline: none;
}

.div-slider text {
  margin-left: 35px;
  margin-right: 35px;
  position: relative;
  background-color: gray;
  border-radius: 10%;
  padding: 5px;
  opacity: 70%;
  word-wrap: break-word;
  width: 18vw;
}

a {
  color: black;
}

a:hover {
  color: white;
}

